import React from 'react'
import { Link } from 'gatsby'
import { GatsbyImage } from 'gatsby-plugin-image'
// import { BsArrowRight } from 'react-icons/bs'

const CaseStudyCard = ({slug, pathToImage, description, overview}) => {
  return (
    <Link to={slug}>
        <div className='w-full h-full relative overflow-hidden drop-shadow-lg'>
          <GatsbyImage image={pathToImage} alt={description} className='thumbnail h-full hover:scale-105 transition duration-500 ease-in-out absolute hover:opacity-25' objectFit='cover' />
          <div className='thumbnail-hover-detail h-full absolute top-0 left-0 p-8 md:p-16 pointer-events-none transition duration-500 ease-in-out'>
              <p className='font-light text-md md:text-2xl lg:text-lg text-white tracking-wider'>{overview}</p>
              {/* <div className='absolute text-white bottom-16 flex items-center'><BsArrowRight className='ml-4 text-4xl' /></div> */}
          </div>
        </div>
    </Link>
  )
}

export default CaseStudyCard
