import React from 'react'
import { graphql } from 'gatsby'
import { getImage } from 'gatsby-plugin-image'
import slugify from 'slugify'

import CaseStudyCard from '../components/CaseStudyCard'

import Seo from '../components/seo'

const IndexPage = ({data:{allContentfulCaseStudies:{nodes:caseStudies}}}) => {
  return (
    <section className='grid grid-cols-1 gap-8 lg:grid-cols-2 lg:mx-8'>
      {caseStudies.map((item) => {
        const pathToImage = getImage(item.thumbnail)
        const slug = slugify(item.title, { lower: true })
        return <article key={item.id} className='w-full h-96 md:h-112 lg:h-112 bg-black'>
          <CaseStudyCard slug={`/${slug}`} pathToImage={pathToImage} description={item.thumbnail.description} overview={item.projectOverview.projectOverview} />
          {/* <Link to={`/${slug}`}>
            <GatsbyImage image={pathToImage} alt={item.thumbnail.description} className='h-112' objectFit='cover' />
          </Link> */}
        </article>
      })}
    </section>
  )
}

export const query = graphql`
  query {
    allContentfulCaseStudies(sort: {order: DESC}) {
      nodes {
        id
        title
        projectOverview {
          projectOverview
        }
        thumbnail {
          gatsbyImageData(placeholder: BLURRED, layout: CONSTRAINED)
          description
        }
      }
    }
  }
`

export const Head = () => <Seo title="Case Studies" />

export default IndexPage
